<template>
  <div class="container">
    <div class="search-zone">
      <input class="search-input" type="text" placeholder="请输入曲谱名" name="score_name" v-model="searchVal" value="1" v-on:focus="focus()">
      <div class="search-icon"></div>
    </div>
    <div class="search-list">
      <ul v-for="(item, index) in scoreList" :key="index">
        <li class="score-item" v-if="item.type == 1">
          <span class="score-name">{{item.name}}</span>
          <div class="score-store" @click="storeScore(index)">收藏</div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { searchScore, scoreScore } from 'api'
import { Toast } from 'mint-ui'
import { getCookie } from '@/libs/utils.js'

export default {
  name: 'Search',
  data () {
    return {
      searchVal: '',
      scoreList: []
    }
  },
  created () {
    let cookie = getCookie('TOKEN')
    if (!cookie) {
      this.$router.push('/bind')
    }
  },
  methods: {
    async storeScore (index) {
      let id = (this.scoreList[index]).id
      let type = (this.scoreList[index]).type
      let params = {
        type: '1',
        id: id,
        resource_type: type
      }
      try {
        let res = await scoreScore(params)
        if (res.data.meta.code === 200) {
          Toast('收藏成功')
        }
      } catch (error) {
        console.log(error)
      }
    },
    focus () {
      if (document.querySelector('input') === document.activeElement) {
        this.searchScoreByName(this.searchVal.trim())
      }
    },
    async searchScoreByName (value) {
      if (!value) {
        this.scoreList = []
      }
      try {
        let name = value
        let params = {
          type: '1',
          score_king: '1',
          offset: '0',
          limit: '100'
        }
        this.scoreList = (await searchScore(name, params)).data.data.list
        console.log(this.scoreList)
      } catch (error) {
        console.log(error || '报错')
      } finally {
        console.log(this.scoreList)
      }
    }
  },
  watch: {
    searchVal () {
      this.focus()
    }
  }
}
</script>
